import Vue from "vue";
import VueRouter from "vue-router";
import Index from "../views/Index.vue";
import Informacion from "../views/Informacion.vue";
import Tarifas from "../views/Tarifas.vue";
import Contacto from "../views/Contacto.vue";
import Actividad from "../views/Actividades.vue"
import Sitios from "../views/Sitios.vue"
Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Index",
        component: Index
    },
    {
        path: "/informacion",
        name: "Informacion",
        component: Informacion
    },
    {
        path: "/tarifas",
        name: "Tarifas",
        component: Tarifas
    },
    {
        path: "/actividades",
        name: "Actividad",
        component: Actividad
    },
    {
        path: "/contacto",
        name: "Contacto",
        component: Contacto
    },
    {
        path: "/lugares-de-interes",
        name: "Sitios",
        component: Sitios
    },
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;