            <h2>¿Porqué los nombres de las chozas?</h2>
            <p>Ausente, .....</p>
            <p>Los nombres de las chozas, o comunmente llamadas pallozas, no son nombres aleatorios. Son nombres de lugares turisticos de la zona de Puebla de Lillo y sus alrededores.</p>
            <router-link to="/informacion" class="enlace">Saber más</router-link>
        </div>
