                },
                {
                    nombre: "Senderismo",
                    imagen: "/Imagenes/Actividades/senderismo.jpg"
                },
            ],
            lugares: [
                {
                    nombre: "Lago de Isoba",
                    imagen: "/Imagenes/Lugares/isoba.jpg"
                },
                {
                    nombre: "Estación de esquí San Isidro",
                    imagen: "/Imagenes/Actividades/esqui.jpg"
                },
                {
                    nombre: "Lago ausente",
                    imagen: "/Imagenes/Lugares/ausente.jpg"
                },
                {
                    nombre: "Los forfogones",
                    imagen: "/Imagenes/Lugares/forfogones.jpg"
                },
            ],
        }
    },
