<template>
  <header>
      <font-awesome-icon @click="activarMenu" class="iconos" icon="bars" />

      <div class="barra-menu" id="menu">
          <router-link class="enlace" @click.native="desactivar" to="/">Inicio</router-link>
          <router-link class="enlace" @click.native="desactivar" to="/informacion">Información</router-link>
          <router-link class="enlace" @click.native="desactivar" to="/tarifas">Tarifas</router-link>
          <router-link class="enlace" @click.native="desactivar" to="/lugares-de-interes">Lugares de interes</router-link>
          <router-link class="enlace" @click.native="desactivar" to="/actividades">Actividades</router-link>
          <!-- <router-link class="enlace" @click.native="desactivar" to="/">A cerca de</router-link> -->
          <router-link class="enlace" @click.native="desactivar" to="/contacto">Contacto</router-link>
      </div>
  </header>
</template>

<script>
export default {
    data() {
        return{
            activo: false,
        }
    },
    methods: {
        activarMenu(){
            if(this.activo){
                this.activo = false;
                document.getElementById("menu").className = 'barra-menu';
            }
            else{
                this.activo = true;
                document.getElementById("menu").className = 'barra-menu-activa';
            }
        },
        desactivar(){
            this.activo = false;
            document.getElementById("menu").className = 'barra-menu';
        }
    }
}
</script>

<style scoped>

header{
    background-color: rgb(132, 194, 37);
    color: white;
    padding-left: 20px;
    height: 60px;
}

.iconos{
    padding-top: 15px;
    color:white;
    font-size: 2em;
    display: none;
}
.barra-menu .enlace{
    height: 60px;
    /* padding-top: 20px; */
    padding-left: 10px;
    padding-right: 20px;
    display:inline-block;
    font-size: 1.2em;
    margin-left: 20px;
    color:white;
    text-decoration: none;
    line-height: 55px;
}

.enlace:hover{
 transform: scale(1.02);
}

@media screen and (max-width:1024px) {
    header{
        padding: 0px;
    }
    .iconos{
        display: block;
        padding-left: 20px;
    }
    .barra-menu{
        display:none;
    }
    .barra-menu-activa{
        display:block;
        position:absolute;
        z-index: 7;
        width: 100%;
        background: rgba(132, 194, 37, 0.6);
        height: max-content;
        margin-top: 10px;
        text-align: center;
        border-top: 2px solid white;
    }
    .enlace{
        width:80%;
        display:block;
        font-size: 1.2em;
        padding-top:15px;
        padding-bottom:15px;
        margin-left: auto;
        margin-right: auto;
        color:white;
        text-align: center;
        text-decoration: none;
        line-height: 55px;
        border-bottom: 2px solid white;
    }
}
</style>